<script>
import LightGallery from "./components/Gallery.vue";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import LocomotiveScroll from "locomotive-scroll";
import "particles.js";

export default {
  components: {
    LightGallery,
  },
  data: () => ({
    services: [
      {
        name: "Development",
        desc:
          "Software Systems, Websites, Algorithms, AI & ML Models and Bots.",
      },
      {
        name: "Data Analysis",
        desc:
          "and other data science related tasks using Artificial Intelligence and Machine Learning Models",
      },
      {
        name: "Graphics Designing",
        desc:
          "Logos, Business Presentations, E-mail Templates, Flyers, Posters, T-Shirts, Cards (Business, Wedding ,..),",
      },
      {
        name: "Web Hosting",
        desc:
          "Shared & Dedicated hosting services, for various type of websites",
      },
      { name: "Consultancy", desc: "On all related tasks and issues" },
    ],
    scroll: null,
    drawer: null,
    items: [
      { title: "Dashboard", icon: "mdi-view-dashboard", to: "/" },
      { title: "About", icon: "mdi-help-box", to: "/about" },
    ],
    right: null,
    colors: [
      "indigo",
      "warning",
      "pink darken-2",
      "red lighten-1",
      "deep-purple accent-4",
    ],
    icons: ["mdi-facebook", "mdi-twitter", "mdi-linkedin", "mdi-instagram"],
    slides: [
      "Development",
      "Data Analysis",
      "Training & Consultance",
      "Graphic Designing",
      "Product Testing",
    ],
  }),
  mounted() {
    const particlesJS = window.particlesJS;

    particlesJS.load("particles-js", "particles.json", function() {
      // console.log("callback - particles.js config loaded");
    });
    gsap.registerPlugin(ScrollTrigger);

    const locoScroll = new LocomotiveScroll({
      el: document.querySelector(".smooth-scroll"),
      smooth: true,
    });

    locoScroll.on("scroll", ScrollTrigger.update);

    // tell ScrollTrigger to use these proxy methods for the ".smooth-scroll" element since Locomotive Scroll is hijacking things
    ScrollTrigger.scrollerProxy(".smooth-scroll", {
      scrollTop(value) {
        return arguments.length
          ? locoScroll.scrollTo(value, 0, 0)
          : locoScroll.scroll.instance.scroll.y;
      }, // we don't have to define a scrollLeft because we're only scrolling vertically.
      getBoundingClientRect() {
        return {
          top: 0,
          left: 0,
          width: window.innerWidth,
          height: window.innerHeight,
        };
      },
      // LocomotiveScroll handles things completely differently on mobile devices - it doesn't even transform the container at all! So to get the correct behavior and avoid jitters, we should pin things with position: fixed on mobile. We sense it by checking to see if there's a transform applied to the container (the LocomotiveScroll-controlled element).
      pinType: document.querySelector(".smooth-scroll").style.transform
        ? "transform"
        : "fixed",
    });

    gsap.from(".logo", {
      // scale: 2,
      ease: "bounce",
      duration: 2,
      // yPercent: 70,
      xPercent: 30,
      // delay: 2,
    });

    let tl = gsap.timeline();
    tl.from(".pritext", {
      yPercent: -100,
      opacity: 0,
      delay: 1,
      ease: "bounce",
    })
      .from(".sectext", {
        yPercent: 100,
        opacity: 0,
        ease: "power2",
      })
      .from(".mysvg", {
        xPercent: 50,
        opacity: 0,
      })
      .from(".scrol", {
        yPercent: -50,
        opacity: 0,
      });

    // gsap.from("#about", {
    //   scrollTrigger: {
    //     trigger: "#about",
    //     scroller: ".smooth-scroll",
    //     start: "top bottom",
    //     end: "bottom top",
    //     onEnter: ({ progress, direction, isActive }) =>
    //       locoScroll.scrollTo("#about"),
    //     onEnterBack: ({ progress, direction, isActive }) =>
    //       locoScroll.scrollTo("#about"),
    //   },
    // });
    // gsap.from("#wdo", {
    //   scrollTrigger: {
    //     trigger: "#wdo",
    //     scroller: ".smooth-scroll",
    //     start: "top bottom",
    //     end: "bottom top",
    //     onEnter: ({ progress, direction, isActive }) =>
    //       locoScroll.scrollTo("#wdo"),
    //     onEnterBack: ({ progress, direction, isActive }) => {
    //       locoScroll.scrollTo("#wdo");
    //       console.log(progress, direction, isActive);
    //     },
    //   },
    // });
    // gsap.from(".serv", {
    //   scrollTrigger: {
    //     trigger: "#wdo",
    //     toggleActions: "restart resume reverse resume",
    //     scroller: ".smooth-scroll",
    //     start: "top +=85%",
    //     end: "bottom top",
    //     scrub: true,
    //   },
    //   ease: "power3",
    //   yPercent: 10,
    // opacity: 0,
    // });

    // gsap.from("#wdone", {
    //   scrollTrigger: {
    //     trigger: "#wdone",
    //     scroller: ".smooth-scroll",
    //     start: "top bottom",
    //     end: "bottom top",
    //     onEnter: ({ progress, direction, isActive }) =>
    //       locoScroll.scrollTo("#wdone"),
    //     onEnterBack: ({ progress, direction, isActive }) =>
    //       locoScroll.scrollTo("#wdone"),
    //   },
    // });

    gsap.from(".bcs", {
      scrollTrigger: {
        trigger: ".contact",
        toggleActions: "restart pause reverse resume",
        scroller: ".smooth-scroll",
        start: "top center",
        end: "bottom bottom",
        scrub: true,
      },
      ease: "power1.Out",
      yPercent: -50,
      opacity: 0,
      stagger: 1,
    });
    // gsap.from(".abouts", {
    //   scrollTrigger: {
    //     trigger: ".abouts",
    //     toggleActions: "restart reverse reverse resume",
    //     start: "top center",
    //     end: "bottom center",
    //     markers: true,
    //     scrub: 1,
    //   },
    //   ease: "power1.Out",
    //   yPercent: -50,
    //   opacity: 0,
    // });

    ScrollTrigger.addEventListener("refresh", () => locoScroll.update());
    window.addEventListener("resize", () => locoScroll.update());

    // after everything is set up, refresh() ScrollTrigger and update LocomotiveScroll because padding may have been added for pinning, etc.
    ScrollTrigger.refresh();

    // let l = document.getElementsByClassName(".link")[0];
    // l.addEventListener("click", function(e) {
    //   e.preventDefault();
    //   console.log("wer");
    //   // locoScroll.scrollTo(e.target.getAttribute("href"));
    // });

    gsap.utils.toArray(".link").forEach(function(a) {
      a.addEventListener("click", function(e) {
        e.preventDefault();
        // console.log("wer");
        locoScroll.scrollTo(e.target.getAttribute("href"));
      });
    });
  },
  updated() {
    console.log("updated");
  },
  methods: {
    drawerW() {
      this.scroll.scrollTo(document.querySelector("#about"));
    },
  },
};
</script>

